.gallery__card{align-self:center;justify-self:center;max-width:35vw;max-height:35vh;padding:.6rem;box-shadow:rgba(50,50,93,.25) 0px 50px 100px -20px,rgba(0,0,0,.3) 0px 30px 60px -30px;border:1px solid #050505;transition:all .2s ease-in-out}.gallery__card:hover{padding:0;transform:scale(1)}@media(max-width: 1024px){.gallery__card{max-width:45vw;max-height:45vh}}@media(max-width: 481px){.gallery__card{max-width:80vw;height:auto}}.gallery__popup__card{display:flex;flex-direction:column;padding-top:1rem;max-width:700px;font-size:1.1rem}.gallery__popup__card p{padding:.2rem}.gallery__popup__image{display:block;margin:0 auto 1rem;max-width:100%}.gallery__popup__title::before{font-weight:bold;content:"Title: "}.gallery__popup__author::before{font-weight:bold;content:"Author: "}.gallery__popup__url{color:rgba(86,110,190,.945)}.gallery__popup__url:hover::after{content:"›"}.gallery__popup__years::before{font-weight:bold;content:"Years of creation: "}.gallery__popup__tag{float:right;color:#858585}.gallery__popup__tag::before{content:"#"}