@import 'variables.scss';

.popup {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;

    background: $popup-wrapper-color;
    z-index: 10;
    
    overflow-y: auto;

    &__body {
        display: flex;
        align-items: center;
        justify-content: center;
        margin: 1.5rem 0;
    }

    &__content {
        font-family: "Courier New", monospace;
    
        background: $primary-color;
        
        position: relative;
        max-width: 800px;
        padding: 1rem 2rem;
    
        align-items: center;
        justify-content: center;
    }

    &__close {
        font-size: 2rem;

        position: absolute;
        right: 10px;
        top: 0;
        
        cursor: pointer;

        transition: .3s;

        &:hover {
            transform: rotate(180deg);
        }
    }
}